import React from 'react';

import { UncontrolledCarousel } from 'reactstrap';

const items = [
    {
        src: require('../Pictures/IT Solutions.jpg'),
        header: 'IT Solutions'
    },
    {
        src: require('../Pictures/Staffing.jpg'),
        header: 'Staffing services'
    },
    {
        src: require('../Pictures/Product Engineering.jpg'),
        header: 'Product Engineering'
    }
];

export default () => <UncontrolledCarousel items={items} />;



