import React from 'react'

import { Container } from 'reactstrap'

import './Styles/Header.css'

const Styles = {
    marginBottom: '-20px',
    BackgroundColor: 'white',
    padding: '70px'
}

export default () => {
    return(
        <React.Fragment>
            <div style={Styles}> 
                <Container fluid>
                    <h1 className='display-3 header' style={{ textAlign: 'center' }}>Contact Us</h1>
                    <br />
                    <p className="lead" style={{ textAlign: 'center' }}> 
                        hr@trioconsys.com | Livermore CA 94550
                    </p>
                    <p className="lead" style={{ textAlign: 'center' }}>
                         2018 TRIOCON Systems Inc.
                    </p>
                </Container>
            </div>
        </React.Fragment>
    );
}


