import React from 'react';

import './Styles/HomePage.css'

import { Jumbotron, Container, Row, Col } from 'reactstrap';

const Styles = {
    paddingTop: '70px',
    marginBottom: '-33px',
    height: '100%',
}

const fontAdjust = {
    fontSize: '30px'
}

export default (props) => {
  return (
      
    <div style={Styles} >
        <Jumbotron fluid>
            <Container fluid style={{ padding: '70px'}}>
                <Row>
                    <Col md="2">
                        {/* Empty space */}
                    </Col>
                    <Col md="8">
                        <h1 className="display-1 header"><span style={{ color: '#0054a6'}}>TRIO</span><span style={{ color: '#fe6423'}}>CON</span> SYSTEMS</h1>
                        <p className="lead para" style={fontAdjust}>
                            is an end to end IT solution, product engineering and staffing services company.
                        </p>
                        <hr />
                        <p className="lead para" style={fontAdjust}>
                            Our clientele focus is on US and Indian geographies. 
                        </p>
                        <hr />
                        <p className="lead para" style={fontAdjust}>
                            Our mission is to identify and nurture top tier talents in varied technology service lines, thus continuing to enable our clients achieve success along their technology roadmap journey.
                        </p>
                    </Col>
                    <Col md="2">
                        {/* Empty space */}
                    </Col>
                </Row>          
            </Container>
        </Jumbotron>
    </div>
  );
};




