import React from 'react'
import { Jumbotron, Container, Row, Col } from 'reactstrap';

import './Styles/CoreAreas.css'
import './Styles/Header.css'

const Styles = {
    paddingBottom: '76px',
    marginBottom: '-40px'
} 

export default () => {
    return (
        <div>
            <Jumbotron fluid  style={Styles}>
                <Container>
                    <h1 className="display-3 header" style={{ textAlign: 'center' }}> Our core areas</h1>
                    <br />
                    <br />
                    <Row style={{textAlign: 'center'}}>
                        <Col md="4" xs="4">
                            <button className='coreComponent' 
                                style={{ backgroundColor: '#0054a6'}} disabled >
                                E-commerce
                            </button>
                        </Col>
                        <Col md="4" xs="4">
                            <button className='coreComponent' 
                                style={{ backgroundColor: '#fe6423'}} disabled >
                                Customer Relationship Management
                            </button>
                        </Col>
                        <Col md="4" xs="4">
                            <button className='coreComponent' 
                                style={{ backgroundColor: '#0054a6'}} disabled >
                                Test Automation
                            </button>
                        </Col>
                    </Row>
                    <br />
                    <br />
                    <Row style={{textAlign: 'center'}}>
                        <Col md="4" xs="4">
                            <button className='coreComponent' 
                                style={{ backgroundColor: '#fe6423'}} disabled >
                                Big Data
                            </button>
                        </Col>
                        <Col md="4" xs="4">
                            <button className='coreComponent' 
                                style={{ backgroundColor: '#0054a6'}} disabled >
                                Business Intelligence
                            </button>
                        </Col>
                        <Col md="4" xs="4">
                            <button className='coreComponent' 
                                style={{ backgroundColor: '#fe6423'}} disabled >
                                Cloud Engineering and Analytics
                            </button>
                        </Col>
                    </Row>
                </Container>
            </Jumbotron>
        </div> 
    );
}


